.navbar {
    height: 75px;
    background: grey;
}

#title, #navbarText, #scores {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center;
}

#red {
    background: red;
}

#green {
    background: green;
}

#grey {
    background: grey;
}
.click-card {
    background-size: cover;
    background-position: 50%;
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    cursor: pointer;
}
#click-game {
    padding-left: 100px;
    padding-right: 100px;
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
}

.header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
}

.game-container {
    padding-top: 75px;
    width: 100%;
    margin: 0 auto;
}
